import { React, useState } from "react";

function Footer() {
  const [user, setuser] = useState({
    email: "",
  });

  let name, value;
  const getUserData = (e) => {
    name = e.target.name;
    value = e.target.value;

    setuser({ ...user, [name]: value });
  };
  const postData = async (e) => {
    e.preventDefault();

    const { email } = user;

    if (email) {
      const responce = await fetch(
        "https://form-4a643-default-rtdb.firebaseio.com/AptoLaunchSubscribe.json",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            email,
          }),
        }
      );
      if (responce) {
        setuser({
          email: "",
        });
        alert("success");
      }
    } else {
      alert("please fill your Email");
    }
  };
  return (
    <footer className="footer style-1" id="Contact-Us">
      <div className="footer__top">
        <div className="container">
          <div className="row">
            <div className="col-xl-7 col-md-12">
              <div className="content-left">
                <h5 className="fs-22">Don't miss out, Stay updated</h5>
                <p>Don’t hesitate to subscribe to latest news about ICo</p>

                <form method="POST" id="subscribe-form">
                  <input
                    type="email"
                    placeholder="Enter your email"
                    name="email"
                    value={user.email}
                    onChange={getUserData}
                    id="subscribe-email"
                  />
                  <button
                    className="btn-action style-2"
                    type="submit"
                    id="subscribe-button"
                    onClick={postData}
                  >
                    {" "}
                    <span className="effect">SUBSCRIBE</span>
                  </button>
                </form>
              </div>
            </div>
            <div className="col-xl-5 col-md-12">
              <div className="content-right">
                <p>
                  Don’t hesitate to subscribe to latest news about ICo markets
                  as well as crucial financial knowledge to become successful
                  investors globally
                </p>
                <ul className="list-social">
                  <li>
                    <span className="icon-twitter"></span>
                  </li>
                  <li>
                    <span className="icon-facebook"></span>
                  </li>
                  <li>
                    <span className="icon-place"></span>
                  </li>
                  <li>
                    <span className="icon-youtobe"></span>
                  </li>
                  <li>
                    <span className="icon-tiktok"></span>
                  </li>
                  <li>
                    <span className="icon-reddit"></span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="footer__bottom">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <p>
                © 2023. All rights reserved by {"APTOS"}
                <a href="../">Launcher</a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
