import React from "react";
import VideoPlay from "./VideoPlay";
import tc1 from "../assets/images/dom.png";
import tc2 from "../assets/images/icon/bitcoin2.png";
import tc3 from "../assets/images/icon/eth.png";
import tc4 from "../assets/images/icon/apt.png";
import tc5 from "../assets/images/layout/banner01.png";
import tc6 from "../assets/images/layout/nex5.png";

const TokenChampa = () => {
  //btc
  const changeBTC = async () => {
    let ws = await new WebSocket(
      "wss://stream.binance.com:9443/ws/btcusdt@aggTrade"
    );
    // btc,eth,bnb
    // https://github.com/binance-us/binance-official-api-docs/blob/master/web-socket-streams.md
    // etheur@trade
    let sp = document.getElementById("btc");
    let price;
    let lp = null; //lastprice
    ws.onmessage = (event) => {
      let stockobj = JSON.parse(event.data);
      price = parseFloat(stockobj.p).toFixed(0);
      sp.innerText = price;
      sp.style.color =
        !lp || lp === price ? "green" : price > lp ? "green" : "red";
      lp = price;
    };
  };

  //eth
  const changeETH = async () => {
    let wse = await new WebSocket(
      "wss://stream.binance.com:9443/ws/ethusdt@aggTrade"
    );
    // btc,eth,bnb
    // https://github.com/binance-us/binance-official-api-docs/blob/master/web-socket-streams.md
    // etheur@trade
    let spe = document.getElementById("eth");
    let pricee;
    let lpe = null; //lastprice
    wse.onmessage = (event) => {
      let stockobje = JSON.parse(event.data);
      pricee = parseFloat(stockobje.p).toFixed(0);
      spe.innerText = pricee;
      spe.style.color =
        !lpe || lpe === pricee ? "green" : pricee > lpe ? "green" : "red";
      lpe = pricee;
    };
  };

  //bnb
  const changeAPT = async () => {
    let wsb = await new WebSocket(
      "wss://stream.binance.com:9443/ws/aptusdt@aggTrade"
    );
    // btc,eth,bnb
    // https://github.com/binance-us/binance-official-api-docs/blob/master/web-socket-streams.md
    // etheur@trade
    let spb = document.getElementById("apt");
    let priceb;
    let lpb = null; //lastprice
    wsb.onmessage = (event) => {
      let stockobjb = JSON.parse(event.data);
      priceb = parseFloat(stockobjb.p).toFixed(1);
      spb.innerText = priceb;
      spb.style.color =
        !lpb || lpb === priceb ? "green" : priceb > lpb ? "green" : "red";
      lpb = priceb;
    };
  };
  changeBTC();
  changeETH();
  changeAPT();
  const contractFN = (e) => {
    e.preventDefault();
    alert("😢opps! CONTRACT ADDRESS NOT RELEASED YET!");
  };

  return (
    <section className="banner-top style-2">
      <div className="container">
        <div className="row">
          <div className="col-xl-6 col-md-12">
            <div className="banner-top__content">
              <h2 className="title">
                Mint, sell, and <span>trade NFTs</span> at scale.
              </h2>
              <p className="desc">
                Outcome-centered products that reduce churn, optimize pricing,
                and grow your subscription business end-to-end.
              </p>
              <div className="button">
                <a
                  href="sign-in.html"
                  className="btn-action style-2"
                  onClick={contractFN}
                >
                  <span>
                    <img draggable="false" src={tc1} alt="panC" /> BUY TOKEN
                  </span>
                </a>
              </div>

              <ul className="content-list">
                <li>
                  <span className="icon-brute1444">
                    <img draggable="false" src={tc2} alt="AL" />
                  </span>
                  <h5 id="btc">--/--</h5>
                  <p>Bitcoin/Usdt</p>
                </li>
                <li>
                  <span className="icon-pegasus3232">
                    <img draggable="false" src={tc3} alt="" />
                  </span>
                  <h5 id="eth">--/--</h5>
                  <p>ETH/Usdt</p>
                </li>
                <li>
                  <span className="icon-bear-face434234324">
                    <img draggable="false" src={tc4} alt="AL" />
                  </span>
                  <h5 id="apt">--/--</h5>
                  <p>APT/Usdt</p>
                </li>
              </ul>
            </div>
          </div>

          <div className="col-xl-6 col-md-12">
            <div className="banner-top__sale">
              <img
                draggable="false"
                id="scanf"
                src={
                  window.matchMedia("(max-width: 700px)").matches ? tc6 : tc5
                }
                alt="APTOS"
              />

              <VideoPlay />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
export default TokenChampa;
