import { React, useEffect } from "react";
import Swiper from "swiper/bundle";
import $ from "jquery";
import "swiper/css/bundle";
import "swiper/css";
import "swiper/swiper-bundle.min.css";
import "swiper/css/autoplay";
import ns1 from "../assets/images/item/1.jpg";
import ns2 from "../assets/images/item/2.jpg";
import ns3 from "../assets/images/item/3.jpg";
import ns4 from "../assets/images/item/4.jpg";
import ns5 from "../assets/images/item/5.jpg";
import ns6 from "../assets/images/item/6.jpg";
import ns7 from "../assets/images/item/7.jpg";
import ns8 from "../assets/images/item/8.jpg";
import ns9 from "../assets/images/item/11.jpg";
import ns10 from "../assets/images/item/12.jpg";
import ns11 from "../assets/images/item/13.jpg";

function NftSlider() {
  useEffect(() => {
    var swiper = new Swiper(".swiper-about-1", {
      loop: true,
      direction: "vertical",
      slidesPerView: 2,
      spaceBetween: 30,
      autoplay: {
        delay: 1,
      },
      speed: 2000,
    });

    $(".swiper-about-1").hover(
      function () {
        this.swiper.autoplay.stop();
      },
      function () {
        this.swiper.autoplay.start();
      }
    );
  }, []);

  useEffect(() => {
    // <-- Here

    var swiper = new Swiper(".swiper-about-2", {
      loop: true,
      direction: "vertical",
      slidesPerView: 2,
      spaceBetween: 30,
      autoplay: {
        delay: 1,
        reverseDirection: true,
      },
      speed: 2000,
    });
    $(".swiper-about-2").hover(
      function () {
        this.swiper.autoplay.stop();
      },
      function () {
        this.swiper.autoplay.start();
      }
    );
  }, []);

  return (
    <section className="about style-6" id="AI-NFT">
      <div className="container">
        <div className="row">
          <div className="col-xl-5 col-md-12">
            <div className="block-text">
              <h2 className="title">WHY ADOPT AN APPROVING CORGI?</h2>
              <p className="text fs-20">
                Well, none of their negativity will keep you down! Because you
                have the only approval you need waiting for you back at your
                computer… your lovable, adorable Approving Corgi!
              </p>
              <a href="../" className="btn-action">
                <span>SEE LOYALTY PROGRAM</span>
              </a>
            </div>
          </div>
          <div className="col-xl-7 col-md-12">
            <div className="about__content">
              <div className={`swiper swiper-about-1`}>
                <div className="swiper-wrapper">
                  <div className="swiper-slide">
                    <img draggable="false" src={ns1} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns2} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns3} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns4} alt="APTOS" />
                  </div>
                </div>
              </div>
              <div className={`swiper swiper-about-2`}>
                <div className="swiper-wrapper">
                  <div className="swiper-slide">
                    <img draggable="false" src={ns5} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns6} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns7} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns8} alt="APTOS" />
                  </div>
                </div>
              </div>
              <div className={`swiper swiper-about-1`}>
                <div className="swiper-wrapper">
                  <div className="swiper-slide">
                    <img draggable="false" src={ns9} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns10} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns11} alt="APTOS" />
                  </div>
                  <div className="swiper-slide">
                    <img draggable="false" src={ns2} alt="APTOS" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default NftSlider;
